<template>
    <div>
        <el-breadcrumb separator="/" class="mb30">
            <el-breadcrumb-item :to="{ path: '/layout/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item >新闻列表页</el-breadcrumb-item>
        </el-breadcrumb>

        <el-card>

            <el-table :data="formData" stripe style="width: 100%" align="center">
                <el-table-column prop="title" label="新闻标题" width="140" align="center"/>
                <el-table-column prop="author.nicheng"  label="作者" width="140"  align="center"/>
                <el-table-column label="新闻头图" width="120" align="center" >
                    <template slot-scope="scope">
                        <img :src="scope.row.img" class="avatar-item" alt="">
                    </template>
                </el-table-column>
                <el-table-column  label="新闻内容" width="260" align="center">
                    <template slot-scope="scope">
                        <div class="table-content-ys" v-text="scope.row.contentText"></div>
                    </template>
                </el-table-column>
                <el-table-column prop="type.title"  label="新闻分类" width="120"  align="center"/>
                <el-table-column prop="look_num"  label="浏览量" width="80"  align="center"/>
               
                <el-table-column label="操作" width="180" align="center">
                    <template slot-scope="scope">
                        <el-button @click="handlelook(scope.row._id)" type="primary" size="small">编辑</el-button>
                        <el-button @click="handledel(scope.row._id)" type="danger" size="small">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            
            <el-pagination
            background
            class="page-ys"
            layout="prev, pager, next"
            @current-change="pageing"
            :page-size="page.size"
            :total="count">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                formData:[],
                count:30,
                page:{
                    pn:1,
                    size:4
                }
            }
        },
        methods:{
            pageing(pn){ //翻页
                this.page.pn = pn
                this.getData()
            },
            getData(){
                this.$axios.get('/ddyj/news',this.page).then(res => {
                    this.formData = res.data
                    this.count = res.count
                })
            },
            handlelook(id){
                this.$router.push(`/layout/revisenews/${id}`)
            },
            handledel(id){
                this.$confirm('此操作将永久删除该新闻, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                    }).then(() => {
                        this.$axios.delete(`/ddyj/news/${id}`).then( res => {
                            if(res.code == 200){
                                this.$message.success(res.msg);
                                this.getData()
                            }else if(res.code == 401){
                                this.$message.warning(res.msg)
                            }
                        })
                    }).catch(() => {
                        this.$message.info('已取消删除');          
                    });

            }
        },
        created(){
            this.getData()
        }

    }
</script>

<style scoped>

</style>


